 .patient_box_title .down,.alert_box_title .down{
  position: relative;
  left:40px;
} 
.patient_box_title .up,.alert_box_title .up{
  position: relative;
  left:50px;
} 

.patients span{
  display: block;
  text-align: center;
  margin-top: 5px;
  height: 60px;
  padding: 15px;
  box-sizing: border-box;
  border: 5px solid 	#4682B4;  
  border-radius: 5px;
  border-style:ridge;
  background-color: azure  ;
  
}
/* Doctor ui  ends*/


