button{
  cursor: pointer;
}

/* For login/signup buttons */
.in {
	box-shadow: 0px 10px 14px -7px #084a54;
	background:linear-gradient(to bottom, #0b1214 5%, #65b5bf 100%);
	background-color:#0b1214;
	border-radius:8px;
	display:inline-block;
	cursor:pointer;
	color:#edf0f2;
	font-family:Arial;
	font-size:20px;
	font-weight:bold;
	padding:11px 25px;
	text-decoration:none;
	text-shadow:0px 1px 0px #239ac2;
}
.in:hover {
	background:linear-gradient(to bottom, #65b5bf 5%, #0b1214 100%);
	background-color:#65b5bf;
}
.in:active {
	position:relative;
	top:1px;
}